<template>
  <div>
    <transition>
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
    </transition>
    <transition>
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </transition>
    <tab-bottom></tab-bottom>
  </div>

</template>

<script>

    import TabBottom from '../components/TabBottom'

    export default {
      components:{
        TabBottom
      }
    }
</script>

<style lang="scss" scoped>

</style>



